.admin-profile {
    display: flex;
    flex-direction: column;
    font-family: Arial, sans-serif;
  }
  
  .admin-profile .navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 20px;
    background-color: #333;
    color: #fff;
  }
  
  .admin-profile .brand {
    font-size: 24px;
    font-weight: bold;
    cursor: pointer;
  }
  
  .admin-profile .nav-links {
    display: flex;
    align-items: center;
  }
  
  .admin-profile .nav-link {
    margin-left: 20px;
    cursor: pointer;
  }
  
  .admin-profile .profile-pic {
    margin-left: 20px;
    border-radius: 50%;
    width: 30px;
    height: 30px;
    cursor: pointer;
  }
  
  .admin-profile .profile-box {
    margin: 20px auto;
    padding: 20px;
    border-radius: 10px;
    background-color: #f9f9f9;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
    position: relative;
    width: 70%;
    max-width: 800px;
  }
  
  .admin-profile .profile-banner {
    background-color: #b5651d;
    height: 10%;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    position: relative;
    text-align: center;
  }
  
  .admin-profile .profile-picture {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    position: absolute;
    top: calc(10% - 50px);
    left: calc(50% - 50px);
    border: 3px solid #fff;
  }
  
  .admin-profile .profile-details {
    margin-top: 60px;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .admin-profile .form-field {
    margin: 10px 0;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
  }
  
  .admin-profile .form-field label {
    font-weight: bold;
    margin-bottom: 5px;
  }
  
  .admin-profile .form-field input {
    padding: 5px;
    border: 1px solid #ccc;
    border-radius: 5px;
    width: 100%;
  }
  
  .admin-profile .edit-button {
    position: absolute;
    top: 20px;
    right: 20px;
    padding: 10px;
    background-color: #333;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .admin-profile .edit-button:hover {
    background-color: #555;
  }