.admin-contact {
    display: flex;
    flex-direction: column;
    font-family: Arial, sans-serif;
  }
  
  .navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 20px;
    background-color: #333;
    color: #fff;
  }
  
  .brand {
    font-size: 24px;
    font-weight: bold;
    cursor: pointer;
  }
  
  .nav-links {
    display: flex;
    align-items: center;
  }
  
  .nav-link {
    margin-left: 20px;
    cursor: pointer;
  }
  
  .profile-pic {
    margin-left: 20px;
    border-radius: 50%;
    width: 30px;
    height: 30px;
    cursor: pointer;
  }
  
  .contact-content {
    padding: 20px;
  }
  
  .contact-content h2 {
    margin-top: 80px;
    text-align: center;
    margin-bottom: 20px;
  }
  
  .contact-box {
    display: flex;
    border: 1px solid #ccc;
    border-radius: 5px;
    padding: 20px;
    background-color: #f9f9f9;
    margin-top: 50px;
  }
  
  .left-box,
  .right-box {
    width: 50%;
    padding: 20px;
  }
  
  .left-box {
    border-right: 1px solid #ccc;
  }
  
  .contact-form {
    display: flex;
    flex-direction: column;
  }
  
  .form-field {
    margin-bottom: 10px;
  }
  
  .form-field input,
  .form-field textarea {
    width: 100%;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
  }
  
  button {
    padding: 10px;
    background-color: #333;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  button:hover {
    background-color: #555;
  }
  